<template>
  <div v-if="!loading">
    <loading-component :value="loading"></loading-component>
    <v-layout class="wrap-layout mb-3">
      <v-flex xs12 m-3>
        <v-layout row wrap class="pa-1">
          <v-flex xs1>
            <div class="headline font-weight-bold">
              <v-avatar size="100%">
                <img :src="api.GET_IMAGE(image)" alt="Michael Wang"/>
              </v-avatar>
            </div>
          </v-flex>
          <v-flex xs11 class="pl-3">
            <div>
              <v-layout row wrap class="pa-1">
                <v-flex xs11>
                  <h2 class="primary--text">{{ this.post.user_name }}
                    <span v-if="filter !== 'group_posts'" class="black--text font-weight-light">
                      <v-icon>arrow_forward</v-icon>
                      <span class="secondary--text">{{ this.post.group_name }}</span>
                    </span>
                  </h2>
                </v-flex>
                <v-flex xs1>

                  <v-tooltip v-if="mated == false" bottom color="success">
                    <template v-slot:activator="{ on }">
                      <v-scroll-x-transition>
                        <v-icon v-if="mated == false" @click="markAsMated()" v-on="on" class="animate" color="grey"
                                large>
                          check_circle_outline
                        </v-icon>
                      </v-scroll-x-transition>
                    </template>
                    <span color="green">Mark as Mated!</span>
                  </v-tooltip>
                  <v-scroll-x-transition>
                    <v-icon class="animate" v-if="mated == true" color="success" large>check_circle</v-icon>
                  </v-scroll-x-transition>
                </v-flex>
              </v-layout>
              <p>{{ this.post.content }}</p>
            </div>
          </v-flex>
          <v-flex xs12>
            <v-card-text class="m-8">
              <v-divider></v-divider>
              <v-layout row wrap>
                <v-flex xs2>
                  <v-btn flat color="secondary">
                    <v-icon class="mr-2">thumb_up</v-icon>
                    like
                  </v-btn>
                </v-flex>
                <v-flex xs2>
                  <v-btn flat color="accent">
                    <v-icon class="mr-2">chat</v-icon>
                    comment
                  </v-btn>
                </v-flex>
                <v-flex xs4></v-flex>
                <v-flex xs2 style="margin: auto; text-align: right">
                  <h3 class="post-info">{{ this.post.likes_counter }} likes</h3>
                </v-flex>
                <v-flex xs2 style="margin:auto 0; text-align: right">
                  <h3 class="mr-2 post-info">{{ this.post.comments_counter }} comments</h3>
                </v-flex>
              </v-layout>
              <v-divider></v-divider>
            </v-card-text>
            <div style="padding-left: 30px;">
              <h3 class="mb-3">comments</h3>
              <comment></comment>
              <comment></comment>
            </div>
          </v-flex>
        </v-layout>
      </v-flex>
    </v-layout>
  </div>
</template>

<script>
import Comment from "@/components/Comment"
import LoadingComponent from "./LoadingComponent";

export default {
  name: "Post",
  components: {
    LoadingComponent,
    Comment
  },
  props: ['id', 'filter'],
  data() {
    return {
      loading: true,
      mated: false,
      post: null
    }
  },
  mounted() {
    this.fetchPost();
  },
  methods: {
    markAsMated() {
      this.mated = true;
      window.getApp.$emit('SHOW_TOAST', 'Mated!', 'success');
    },
    fetchPost() {
      this.axios.get(this.api.ACTIONS.POST(this.id))
          .then(res => {
            this.loading = false;
            this.post = res.data.data
          })
    }
  },
  computed: {
    image() {
      return this.$store.state.user.profile_image
    },
  },
}
</script>

<style scoped>
.wrap-layout {
  background-color: white;
  margin-top: 5px;
  border: 1px solid lightgrey;
  box-shadow: 0px 3px 1px -2px rgb(0 0 0 / 20%), 0px 2px 2px 0px rgb(0 0 0 / 14%), 0px 1px 5px 0px rgb(0 0 0 / 12%);
  border-radius: 3px;
  padding: 10px
}

.post-info {
  color: grey;
  font-weight: normal;
  font-size: 16px;
}

</style>